
#hero-background {
	position: absolute;
	width: 250%;
	height: 743px;
	top: -50px;
	left: -75%;
	z-index: -1;
	overflow: hidden;
	background: #01002a;
	background: -moz-linear-gradient(top,  #01002a 75%, #0f2e4f 100%);
	background: -webkit-linear-gradient(top,  #01002a 75%,#0f2e4f 100%);
	background: linear-gradient(to bottom,  #01002a 75%,#0f2e4f 100%);
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#01002a', endColorstr='#0f2e4f',GradientType=0 );
}
#hero-background {
	position: fixed;
	width: 100%;
	height: 100%;
	left: 0;
	top: 0;
}
#hero-background .drip {
	width: 15px;
	height: 200px;
	opacity: 0.75;
	background: -moz-linear-gradient(top, transparent 10%, rgba(44,169,183,1) 100%);
	background: -webkit-linear-gradient(top, transparent 10%, rgba(44,169,183,1) 100%);
	background: linear-gradient(to bottom, transparent 10%, rgba(44,169,183,1) 100%);
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#0001002a', endColorstr='#2ca9b7',GradientType=0 );
	border-radius: 25px;
	display: block;
	position: absolute;
	animation: falling-rain 35s infinite;
}

/* Floating bubbles */

@keyframes falling-rain {
    0% {
        -webkit-transform: translateY(-500px);
        transform: translateY(-500px);
    }
    100% {
        -webkit-transform: translateY(1200px);
        transform: translateY(1200px);
    }
}
